import * as React from 'react';

import './footer.scss';
import {useState} from "react";

import logo from '../../assets/logo.png'

import tg from './tg.svg';

import telegram from './telegram.png';
import instagram from './inst.png';
import twitter from './twitter.png';

import { Link } from 'react-router-dom';

const DEFAULT_CLASSNAME = 'footer';

export const Footer = ({ isLoginPage }) => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');

    return (!isLoginPage &&
        <div className={`${DEFAULT_CLASSNAME}_wrapper`}>
            <div className={DEFAULT_CLASSNAME}>
                <div className={`${DEFAULT_CLASSNAME}_header`}>
                    <div className={`${DEFAULT_CLASSNAME}_header-text`}>{'     '}</div>
                    <input value={name} onChange={(e) => setName(e.currentTarget.value)} type={'text'} placeholder={'*'} />
                    <input value={email} onChange={(e) => setEmail(e.currentTarget.value)} type={'text'} placeholder={'E-mail*'} />
                    <div className={`${DEFAULT_CLASSNAME}_header-btn`}>
                        <img src={tg} alt={'telegram'} />
                    </div>
                </div>
                <div className={`${DEFAULT_CLASSNAME}_line`} />

                <div className={`${DEFAULT_CLASSNAME}_content`}>
                    <div className={`${DEFAULT_CLASSNAME}_content_left`}>
                        <div className={`${DEFAULT_CLASSNAME}_content_left-logo`}>
                            <img src={logo} alt={'logo'} />
                        </div>
                        <div className={`${DEFAULT_CLASSNAME}_content_left-text`}>{"       "}</div>
                        <div className={`${DEFAULT_CLASSNAME}_content_left-links`}>
                            <div className={`${DEFAULT_CLASSNAME}_link`}>
                                <img src={instagram} alt={'footer-link'}/>
                            </div>
                            <div className={`${DEFAULT_CLASSNAME}_link`}>
                                <img src={telegram} alt={'footer-link'}/>
                            </div>
                            <div className={`${DEFAULT_CLASSNAME}_link`}>
                                <img src={twitter} alt={'footer-link'}/>
                            </div>
                        </div>
                    </div>
                    <div className={`${DEFAULT_CLASSNAME}_content_navigation`}>
                        <div className={`${DEFAULT_CLASSNAME}_content_navigation-header`}>{""}</div>
                        <Link to={'/'} className={`${DEFAULT_CLASSNAME}_content_navigation-item`}>{""}</Link>
                        <Link to={'/about'} className={`${DEFAULT_CLASSNAME}_content_navigation-item`}>{" "}</Link>
                        <Link to={'/contacts'} className={`${DEFAULT_CLASSNAME}_content_navigation-item`}>{""}</Link>
                    </div>
                    <div className={`${DEFAULT_CLASSNAME}_content_navigation`}>
                        <div className={`${DEFAULT_CLASSNAME}_content_navigation-header`}>{""}</div>
                        <div className={`${DEFAULT_CLASSNAME}_content_navigation-item`}>{""}</div>
                        <div className={`${DEFAULT_CLASSNAME}_content_navigation-item`}><span>{"+375(44) 999-99-99"}</span><span>{"+375(44) 999-99-99"}</span></div>
                        <div className={`${DEFAULT_CLASSNAME}_content_navigation-item`}>{"E-mail"}</div>
                        <div className={`${DEFAULT_CLASSNAME}_content_navigation-item`}>{"qwertyqwerty@gmail.com"}</div>
                    </div>
                </div>

                <div className={`${DEFAULT_CLASSNAME}_info`}>
                    <div>{' 2022   .   .            . '}</div>
                    <div>
                        <Link to={"/billing"}><span>{''}</span></Link>
                        <Link to={"/docs"}><span>{' '}</span></Link>
                    </div>
                </div>
            </div>
        </div>
    )
}