import * as React from 'react';

import './login.scss';
import google from './assets/google.png'
import trifecta from './assets/trifecto.png';

const DEFAULT_CLASSNAME = 'login';

const loginContent = (
    <>
        <input type={"text"} placeholder={"E-mail*"} />
        <input type={"password"} placeholder={" *"} />
    </>
)

const registerContent = (
    <>
        <div className={`${DEFAULT_CLASSNAME}_header`}>
            <input type={"text"} placeholder={"*"} />
            <input type={"text"} placeholder={"*"} />
        </div>

        <input type={"text"} placeholder={"E-mail*"} />
        <input type={"password"} placeholder={"  (. 8 )*"} />
        <input type={"password"} placeholder={" *"} />

        <div className={`${DEFAULT_CLASSNAME}_footer`}>
            <select placeholder={""}>
                <option></option>
                <option></option>
                <option></option>
            </select>
            <input className={'referal'} type={"text"} placeholder={" "} />
        </div>

    </>
)

export const Login = ({ setIsLoggedIn, isRegisterMode = true }) => {
    return (
        <div className={`${DEFAULT_CLASSNAME}_wrapper`}>
            <div className={DEFAULT_CLASSNAME}>
                <div className={`${DEFAULT_CLASSNAME}_title`}>{isRegisterMode ? "" : ""}</div>

                {isRegisterMode ? registerContent : loginContent}

                <button onClick={() => setIsLoggedIn(true)} className={`${DEFAULT_CLASSNAME}_btn`}>{isRegisterMode ? "" : ""}</button>
                <button onClick={() => setIsLoggedIn(true)} className={`${DEFAULT_CLASSNAME}_btn google-btn`}><img src={google} alt={'google-logo'} /> {"  Google"}</button>

                <img className={`${DEFAULT_CLASSNAME}_trifecta`} src={trifecta} alt={'trifecta'} />
            </div>
        </div>
    )
}